import Vue from 'vue'
import App from './App.vue'


Vue.component('todo-item', {
  props: ['todo'],
  template: '<div class="box">{{ todo.text }}</div>'
})

var app = new Vue({
  el: '#app',
  data: {
    menu: [
      { id: 0, text: 'About' },
      { id: 1, text: "Works" },
      { id: 2, text: "po" },
    ],
  },
  methods: {
    a: function () {
      this.menu[0].text = ""
      this.menu[1].text = ""
      this.menu[2].text = ""
      this.menu[3].text = ""
    }
  }
})

new Vue({
  el: "#trans",
  data: { 
    view: 'v-a',
  },
  components: {
    'v-a': { template: '<div>Component A</div>' },
    'v-b': { template: '<div>Component B</div>' }
  }
})

  




